
import {defineComponent} from 'vue'

interface DataType {
    eventX: number
    eventY: number,
    parentElement: HTMLDivElement,
    isSmartPhone: boolean
}


export default defineComponent({
    data(): DataType {
        return {
            eventX: 0,
            eventY: 0,
            parentElement: null as any,
            isSmartPhone: null as any
        }
    },
    props: {
        windowType: {
            type: String,
            required: true
        },
        handleModalWindowHidden: {
            type: Function,
            required: true
        }
    },
    methods: {
        windowFront: function(e: any) {
            const ModalWindows = document.getElementsByClassName("modal-window")
            for (let i = 0; i < ModalWindows.length; i++) {
                const element = ModalWindows[i] as HTMLElement;
                element.style.zIndex = "0";
            }
            const targetWindow = e.target.closest(".modal-window")
            targetWindow.style.zIndex = "1"

        },
        wideWindow: function(e: any) {
            const targetWindow = e.target.closest(".modal-window")
            const targetModalHeader = targetWindow.children[0]
            const wideButton = targetWindow.getElementsByClassName("wideButton")[0]
            const closeButton = targetWindow.getElementsByClassName("closeButton")[0]
 
            if (targetWindow.style.width == "100%" && targetWindow.style.height == "100%") {
                targetWindow.style.width = "50%"
                targetWindow.style.height = "60%"
                targetWindow.style.top = "50%"
                targetWindow.style.left = "50%"
                targetModalHeader.style.pointerEvents = "auto"
            } else {
                targetWindow.style.width = "100%"
                targetWindow.style.height = "100%"
                targetWindow.style.top = "50%"
                targetWindow.style.left = "50%"
                targetModalHeader.style.pointerEvents = "none"
                wideButton.style.pointerEvents = "auto"
                closeButton.style.pointerEvents = "auto"

            }
        },
        closeWindow: function() {
            this.handleModalWindowHidden(this.windowType)
        },
        mouseup: function() {
            document.body.removeEventListener("mousemove", this.mousemove)
        },
        mousedown: function(e: any) {
            const ModalWindows = document.getElementsByClassName("modal-window")
            for (let i = 0; i < ModalWindows.length; i++) {
                const element = ModalWindows[i] as HTMLElement;
                console.log(element)
                element.style.zIndex = "0";
            }
            this.parentElement = e.target.closest(".modal-window")
            this.eventX = e.clientX - this.parentElement.offsetLeft
            this.eventY = e.clientY - this.parentElement.offsetTop
            this.parentElement.style.zIndex = "1"

            document.body.addEventListener("mousemove", this.mousemove)

        },
        mousemove: function(e: any) {
            const moveY = (e.clientY - this.parentElement.offsetTop) - this.eventY
            const moveX = (e.clientX - this.parentElement.offsetLeft) - this.eventX
            if (moveX > 50) {
                document.body.removeEventListener("mousemove", this.mousemove)
            } else {
                this.parentElement.style.left = this.parentElement.offsetLeft + moveX + "px"
            }

            if (moveY > 50) {
                document.body.removeEventListener("mousemove", this.mousemove)
            } else {
                this.parentElement.style.top = this.parentElement.offsetTop + moveY + "px"
            }
        },
        isSmartPhoneChecker: function() {
            if (navigator.userAgent.match(/iPhone|Android.+Mobile/)) {
                return true;
            } else {
                return false;
            }
        }
    }
})
