
  import { defineComponent } from 'vue' 
  import LoadingComponent from './components/LoadingComponent.vue'


  interface LoadingType {
    loading: boolean
  }

  export default defineComponent({
    components: {
      LoadingComponent
    },
    data():LoadingType {
      return {
        loading: true
      }
    },
    methods: {
      toFalse: function() {
        this.loading = false
      }
    },
    mounted() {
      const toFalse = this.toFalse
      document.addEventListener("keyup", function() {
            toFalse()
      });

      setTimeout(() => {
        this.loading = false;
      }, 5500);
    }
  })
