
import { defineComponent } from 'vue';
import ModalWindowVue from './ModalWindow.vue';
import AboutContent from '../modalwindows/AboutContent.vue'
import SkillContent from "../modalwindows/SkillContent.vue"
import LogsContent from "../modalwindows/LogsContent.vue"

interface ModalWindowTypes {
  [key: string]: boolean
}

export default defineComponent({
  components: {
    ModalWindowVue,
    AboutContent,
    SkillContent,
    LogsContent
  },
  data(): ModalWindowTypes{
    return {
      About: false,
      Skill: false,
      Logs: false,
    }
  },
  mounted() {
    const iconElements = document.getElementsByClassName("isModal")
    const iconElementsTrigger = Array.from(iconElements)
    for (let i = 0; i < iconElementsTrigger.length; i++) {
      const element = iconElementsTrigger[i] as HTMLElement;
      if (this.isSmartPhone()) {
        element.addEventListener("click", this.ModalWindowDisplay)    
      } else {
        element.addEventListener("dblclick", this.ModalWindowDisplay)    
      }
    }
  },
  methods: {
    ModalWindowDisplay: function(e: any) {
      this[e.target.dataset.type] = true
    },
    ModalWindowHidden: function(windowType: string) {
      this[windowType] = false
    },
    isSmartPhone: function() {
      if (navigator.userAgent.match(/iPhone|Android.+Mobile/)) {
        return true;
      } else {
        return false;
      }
    }
  }
})
